.header {
    display: flex;
    align-items: center;
    padding: 1em;
    box-shadow: 0px 6px 20px rgba(0, 16, 61, 0.07);
    background-color: var(--dark-blue-color);
    color: var(--white-color);
    & a {
        color: inherit;
        text-decoration: none;
    }
    & .menu,
    & .login,
    & .logout{
        @media screen and (max-width: 500px){
            display: none;
        }
    }
    & .button__open {
        display: none;
        position: relative;
        width: 1.5em;
        height: 1.1em;
        border: none;
        padding: .3em 0;
        margin-left: auto;
        background-color: transparent;
        cursor: pointer;
        transition: .3s;
        @media screen and (max-width: 500px){
            display: block;
        }
        &:hover span,
        &:hover span::after,
        &:hover span::before{
            background: var(--hover-color);
        }
        & > span {
            display: block;
            height: 3px;
            width: 100%;
            background-color: var(--active-color);
            border-radius: .5em;
            transition: background .3s;
            &::after,
            &::before{
                content: '';
                display: block;
                position: absolute;
                height: 3px;
                width: 100%;
                background-color: var(--active-color);
                border-radius: .5em;
                transition: background .3s;
            }
            &::after{
                top: 0;
            }
            &::before{
                bottom: 0;
            }
        }
    }
}